import React from "react";
import _ from "lodash";
import config from "../config";

const routers = _.get(config, "routers");

function HomePage() {
  return (
    <div>
      Welcome to Next.js!
      <h1>Menu</h1>
      <ul>{_renderRouters(routers)}</ul>
    </div>
  );
}

const _renderRouters = routers => {
  if (!_.isEmpty(routers)) {
    return _.map(routers, (item, index) => {
      return <li>{_.get(item, "text")}</li>;
    });
  }
};

export default HomePage;
